/* Product Sales Detail - Professional Styling with CSS Variables Only */

.product-detail-page {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0;
}

.pd-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 2rem;
  gap: 1.5rem;
  flex-wrap: wrap;
}

.pd-header-left {
  flex: 1;
  min-width: 300px;
}

.pd-breadcrumb {
  font-size: 0.875rem;
  color: var(--text-muted);
  margin-bottom: 0.75rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.pd-breadcrumb-link {
  color: var(--primary);
  cursor: pointer;
  text-decoration: none;
  transition: color 0.15s ease;
}

.pd-breadcrumb-link:hover {
  color: var(--primary-dark);
  text-decoration: underline;
}

.pd-product-title {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 0.5rem 0;
}

.pd-product-meta {
  font-size: 0.9375rem;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.pd-date-selector {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  font-size: 0.875rem;
  color: var(--text-secondary);
}

.pd-date-input {
  padding: 0.625rem 0.875rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  background: var(--bg-surface);
  color: var(--text-primary);
  font-size: 0.875rem;
  transition: border-color 0.2s ease;
}

.pd-date-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.1);
}

/* Metrics Grid */
.pd-metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.25rem;
  margin-bottom: 2rem;
}

.pd-metric-card {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 1.5rem;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  position: relative;
  overflow: hidden;
}

.pd-metric-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--primary);
  opacity: 0;
  transition: opacity 0.2s ease;
}

.pd-metric-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
}

.pd-metric-card:hover::before {
  opacity: 1;
}

.pd-metric-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.pd-metric-value {
  font-size: 2rem;
  font-weight: 700;
  color: var(--text-primary);
  line-height: 1;
}

/* Trend Chart */
.pd-chart-section {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  padding: 1.5rem;
  margin-bottom: 2rem;
}

.pd-chart-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
}

.pd-chart-wrapper {
  position: relative;
  width: 100%;
  overflow-x: auto;
}

.pd-chart {
  display: flex;
  align-items: flex-end;
  gap: 0.75rem;
  min-height: 300px;
  padding: 1rem 0;
}

.pd-chart-bar {
  flex: 1;
  min-width: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
}

.pd-bar {
  width: 100%;
  background: var(--primary);
  border-radius: 6px 6px 0 0;
  transition: all 0.3s ease;
  position: relative;
  min-height: 20px;
}

.pd-bar:hover {
  background: var(--primary-dark);
  transform: scaleY(1.05);
}

.pd-bar-value {
  position: absolute;
  top: -1.75rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--text-primary);
  white-space: nowrap;
}

.pd-bar-label {
  font-size: 0.75rem;
  color: var(--text-secondary);
  text-align: center;
  margin-top: 0.5rem;
}

/* Orders Table */
.pd-orders-section {
  background: var(--bg-surface);
  border: 1px solid var(--border);
  border-radius: 10px;
  overflow: hidden;
}

.pd-orders-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  border-bottom: 1px solid var(--border);
  background: var(--bg-main);
}

.pd-orders-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.pd-status-filter {
  padding: 0.5rem 0.75rem;
  border: 1px solid var(--border);
  border-radius: 6px;
  background: var(--bg-surface);
  color: var(--text-primary);
  font-size: 0.875rem;
  transition: border-color 0.2s ease;
}

.pd-status-filter:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.08);
}

.pd-table-wrapper {
  overflow-x: auto;
}

.pd-table {
  width: 100%;
  border-collapse: collapse;
}

.pd-table thead {
  background: var(--bg-main);
  border-bottom: 2px solid var(--border);
}

.pd-table th {
  padding: 1rem 1.25rem;
  text-align: left;
  font-size: 0.8125rem;
  font-weight: 700;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  white-space: nowrap;
}

.pd-table tbody tr {
  border-bottom: 1px solid var(--border);
  transition: background-color 0.15s ease;
}

.pd-table tbody tr:hover {
  background: var(--bg-hover);
}

.pd-table tbody tr:last-child {
  border-bottom: none;
}

.pd-table td {
  padding: 1rem 1.25rem;
  font-size: 0.9375rem;
  color: var(--text-primary);
  vertical-align: middle;
}

/* Status Badges */
.pd-status-badge {
  display: inline-block;
  padding: 0.375rem 0.75rem;
  border-radius: 16px;
  font-size: 0.8125rem;
  font-weight: 600;
  text-align: center;
  line-height: 1;
}

.status-pending {
  background: rgba(2, 136, 209, 0.12);
  color: var(--info);
}

.status-confirmed {
  background: rgba(46, 125, 50, 0.12);
  color: var(--success);
}

.status-shipped {
  background: rgba(237, 108, 2, 0.12);
  color: var(--warning);
}

.status-delivered {
  background: rgba(46, 125, 50, 0.12);
  color: var(--success);
}

.status-cancelled {
  background: rgba(211, 47, 47, 0.12);
  color: var(--error);
}

/* Empty State */
.pd-empty-state {
  text-align: center;
  padding: 4rem 2rem;
  color: var(--text-muted);
}

.pd-empty-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.pd-empty-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
}

/* Loading Skeletons */
.pd-skeleton {
  background: linear-gradient(
    90deg,
    var(--bg-hover) 25%,
    var(--bg-surface) 50%,
    var(--bg-hover) 75%
  );
  background-size: 200% 100%;
  animation: pd-loading 1.5s ease-in-out infinite;
  border-radius: 6px;
}

@keyframes pd-loading {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

.pd-skeleton-card {
  height: 100px;
}

.pd-skeleton-chart {
  height: 300px;
}

.pd-skeleton-row {
  height: 60px;
  margin-bottom: 0.75rem;
}

/* Error State */
.pd-error {
  background: rgba(211, 47, 47, 0.1);
  border: 1px solid var(--error);
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  color: var(--error);
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .pd-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .pd-product-title {
    font-size: 1.5rem;
  }

  .pd-metrics-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .pd-chart {
    min-height: 200px;
  }

  .pd-orders-header {
    flex-direction: column;
    gap: 1rem;
    align-items: stretch;
  }

  .pd-table th,
  .pd-table td {
    padding: 0.75rem 1rem;
    font-size: 0.8125rem;
  }
}
